@import "../variables/variables";
@import "../mixins/fluid-mixin";

.wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  max-width: 2560px;
  margin: 0 auto;

  @media print {
    display: block; } }

.page {
  flex: 1 1 auto; }

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 122rem;
  padding: 0 1.5rem;
  @media (min-width: 768px) {
    padding: 0 4rem; } }
